å¯ä»¥é€‰æ‹©ä¸€æ¡æ•°æ®ï¼Œç‚¹å‡»è®¾ç½®ï¼Œè°ƒå–${process.env.VUE_APP_API_BASE_URL}/api/set?index=0æˆ–è€…é€‰ä¸­çš„ä¸‹æ ‡ API
ç„¶ååˆ·æ–°æ•°æ®
-->

<template>
    <div class="user-selector">
      <h2>é€‰æ‹©ç”¨æˆ·</h2>

      
  
      <!-- æ˜¾ç¤ºåŠ è½½çŠ¶æ€ -->
      <div v-if="loading" class="loading">
        æ­£åœ¨åŠ è½½...
      </div>
  
      <!-- æ˜¾ç¤ºé”™è¯¯ä¿¡æ¯ -->
      <div v-if="error" class="error">
        {{ error }}
      </div>
  
      <!-- æ˜¾ç¤ºæˆåŠŸä¿¡æ¯ -->
      <div v-if="success" class="success">
        {{ success }}
      </div>
  
      <!-- ç”¨æˆ·åˆ—è¡¨ -->
      <form @submit.prevent="handleSet">
        <div v-for="(user, index) in users" :key="index" class="user-item">
          <input
            type="radio"
            :id="'user-' + index"
            name="selectedUser"
            :value="index"
            v-model="selectedIndex"
          />
          <label :for="'user-' + index">
            <img :src="user.avatar" :alt="user.name" class="avatar" />
            <span>{{ user.name }}</span>
          </label>
          <span v-if="user.default" style="color: blue">ï¼ˆé»˜è®¤ï¼‰</span>
        </div>
  
        <!-- è®¾ç½®æŒ‰é’® -->
        <el-button @click="clearUser" style="margin-right: 30px;">æ¸…ç©ºæ•°æ®</el-button>
        <button type="submit" :disabled="selectedIndex === null || setting">
          {{ setting ? 'è®¾ç½®ä¸­...' : 'è®¾ç½®' }}
        </button>
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    name: 'UserSelector',
    data() {
      return {
        users: [],           // å­˜å‚¨ä» API è·å–çš„ç”¨æˆ·æ•°æ®
        selectedIndex: null, // å½“å‰é€‰ä¸­çš„ç”¨æˆ·ä¸‹æ ‡
        loading: false,     // æ•°æ®åŠ è½½çŠ¶æ€
        setting: false,     // è®¾ç½®æ“ä½œçŠ¶æ€
        error: '',           // é”™è¯¯ä¿¡æ¯
        success: '',         // æˆåŠŸä¿¡æ¯
      };
    },
    methods: {
      // è·å–ç”¨æˆ·æ•°æ®
      async fetchUsers() {
        this.loading = true;
        this.error = '';
        try {
          const response = await axios.get(`${process.env.VUE_APP_API_BASE_URL}/api/user`);
          this.users = response.data;
        } catch (err) {
          this.error = 'è·å–ç”¨æˆ·æ•°æ®å¤±è´¥ï¼Œè¯·ç¨åé‡è¯•ã€‚';
          console.error(err);
        } finally {
          this.loading = false;
        }
      },
      async clearUser() {
        // ç¡®è®¤æ˜¯å¦æ¸…ç©ºæ•°æ®
        if (!window.confirm('ç¡®å®šè¦æ¸…ç©ºç”¨æˆ·æ•°æ®å—ï¼Ÿ')) return;
        
        this.loading = true;
        this.error = '';
        try {
          const response = await axios.get(`${process.env.VUE_APP_API_BASE_URL}/api/clear`);
          this.users = response.data;
        } catch (err) {
          this.error = 'æ¸…ç©ºç”¨æˆ·æ•°æ®å¤±è´¥ï¼Œè¯·ç¨åé‡è¯•ã€‚';
          console.error(err);
        } finally {
          this.loading = false;
        }
      },    
      // è®¾ç½®é€‰ä¸­çš„ç”¨æˆ·
      async handleSet() {
        if (this.selectedIndex === null) return;
  
        this.setting = true;
        this.error = '';
        this.success = '';
  
        try {
          const response = await axios.get(`${process.env.VUE_APP_API_BASE_URL}/api/set`, {
            params: { index: this.selectedIndex },
          });
  
          if (response.data && response.data.message) {
            this.success = response.data.message;
          } else {
            this.success = 'ç”¨æˆ·å·²æˆåŠŸè®¾ç½®ã€‚';
          }
  
          // åˆ·æ–°ç”¨æˆ·æ•°æ®
          await this.fetchUsers();
          // é‡ç½®é€‰æ‹©
          this.selectedIndex = null;
        } catch (err) {
          this.error = 'è®¾ç½®ç”¨æˆ·æ•°æ®å¤±è´¥ï¼Œè¯·ç¨åé‡è¯•ã€‚';
          console.error(err);
        } finally {
          this.setting = false;
        }
      },
    },
    mounted() {
      this.fetchUsers();
    },
  };
  </script>
  
  <style scoped>
