      this.currentQuestionIndexInNav = Number(this.questions[this.currentQuestionIndex].index) - 1;
    },
    goToQuestion(index) {
      // è·å–quetions.index = index åœ¨questionsä¸­çš„ç´¢å¼•
      // console.log("index", index);
      this.currentQuestionIndex = this.questions.findIndex(
        (question) => question.index === String(index + 1)
      );
      this.currentQuestionIndexInNav = index;
      if (this.currentQuestion.warning) {
        this.currentQuestion.warning = false;
      }
      // æ›´æ–°è¿›åº¦æ¡ç™¾åˆ†æ¯” 
      this.progress = this.progressPercentage;

      // console.log("this.currentQuestionIndex", this.currentQuestionIndex);
    },
    handleImageUpload(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.$set(this.answers, this.currentQuestionIndex, e.target.result);
      };
      reader.readAsDataURL(file.raw);
    },
    submitSurvey() {
      // æ ¡éªŒæ˜¯å¦æ‰€æœ‰é—®é¢˜éƒ½å·²å›ç­”ï¼Œä¸æ ¡éªŒéšè—é—®é¢˜, å¹¶å°†å¯¹åº”warningç½®ä¸ºtrue
      let isAllAnswered = true;
      for (let i = 0; i < this.questions.length; i++) {
        if (!this.questions[i].hidden) {
          // å¤šé€‰é¢˜çš„æƒ…å†µ
          if (this.questions[i].type === "multiple-choice") {
            if (this.answers[i].length === 0) {
              this.questions[i].warning = true;
              isAllAnswered = false;
            }
          } else {
            if (this.answers[i] === undefined || this.answers[i].trim() === "") {
              console.log("i", i);
              this.questions[i].warning = true;
              isAllAnswered = false;
            }
          }
        }
      }
      // æœ€åä¸€ä¸ªé—®é¢˜çš„warning
      this.questions[this.questions.length - 1].warning = false;
      // å¼ºåˆ¶æ›´æ–°ç»„ä»¶çŠ¶æ€
      this.questions = [...this.questions];
      // ç¬¬19é¢˜å’Œç¬¬20é¢˜å¿…é¡»å¡«å†™
      if (this.answers[18] === undefined || this.answers[18].trim() === "") {
        this.$message.error("è¯·å¡«å†™å§“åï¼");
        return
      }
      if (this.answers[19] === undefined || this.answers[19] === null) {
        this.$message.error("è¯·ä¸Šä¼ ä¸ªäººç…§ç‰‡ï¼");
        return
      }
      if (!isAllAnswered) {
        this.$message.error("ä½ æ²¡æœ‰å®Œæˆå…¨éƒ¨çš„é¢˜ç›®å“¦ï¼Œè¿™æ ·æ— æ³•é€šè¿‡æˆ‘ä»¬çš„æµ‹è¯•ï¼");
        // return;
      }
      console.log("æäº¤é—®å·ç­”æ¡ˆï¼š", this.answers);
      this.endSituation = -1;
      // æäº¤ç­”æ¡ˆåˆ°åç«¯
      axios
        .post(this.submitUrl, { answers: this.answers })
        .then((response) => {
          // console.log("æäº¤æˆåŠŸï¼š", response.data);
          const results = response.data.results;
          console.log(results)
          this.$message.success("æäº¤æˆåŠŸï¼");
          // å¦‚æœé•¿åº¦ä¸º20ï¼Œåˆ™è¯´æ˜è¿›å…¥æ™®é€šæ¨¡å¼
          if (results.length === 20) {
            // è®¡ç®—æ­£ç¡®æ•°
            this.correctNumber = 0
            for (let i = 0; i < results.length; i++) {
              if (results[i].correct) {
                this.correctNumber++;
              }
            }
            if (this.correctNumber == 14) {
              this.endSituation = 1;
            } else{
              this.endSituation = 0;
            }
            
            
            // this.$router.push({ name: "NormalResult", params: { results } });
          } else {
            // åˆ¤æ–­9-12é¢˜ï¼Œä¹Ÿå°±æ˜¯éšè—é¢˜ç›®æ˜¯å¦å…¨éƒ¨æ­£ç¡®
            let isAllCorrect = true;
            for (let i = 0; i < results.length; i++) {
              if (!results[i].correct) {
                isAllCorrect = false;
                break;
              }
            }
            if (isAllCorrect) {
              this.endSituation = 3;
            } else {
              this.endSituation = 2;
            }
            // this.$router.push({ name: "SpecialResult", params: { results } });
          }
          this.currentQuestionIndex = this.questions.length;
          this.currentQuestionIndexInNav = -1;
        })
        .catch((error) => {
          console.error("æäº¤å¤±è´¥ï¼š", error);
          this.$message.error("æäº¤å¤±è´¥ï¼Œè¯·é‡è¯•ï¼");
        })
      // å‘é€åˆ°
      this.currentQuestionIndex++;
    },

    // å›¾ç‰‡ä¸Šä¼ ç›¸å…³
    /**
     * ä¸Šä¼ æˆåŠŸçš„å›è°ƒ
     * @param {Object} response - åç«¯è¿”å›çš„å“åº”æ•°æ®
     * @param {File} file - ä¸Šä¼ çš„æ–‡ä»¶å¯¹è±¡
     */
     handleAvatarSuccess(response, file) {
      console.log(file)
      if (response.url) {
        this.answers[this.currentQuestionIndex] = response.url;
        console.log('å›¾ç‰‡ä¸Šä¼ æˆåŠŸï¼ŒURL:', response.url);
        this.$message.success('å›¾ç‰‡ä¸Šä¼ æˆåŠŸï¼');
      } else {
        this.$message.error('ä¸Šä¼ å¤±è´¥ï¼šæœªè¿”å›å›¾ç‰‡ URL');
      }
      // å¼ºåˆ¶æ›´æ–°ç»„ä»¶çŠ¶æ€
      this.answers = [...this.answers];
    },
    
    /**
     * ä¸Šä¼ é”™è¯¯çš„å›è°ƒ
     * @param {Object} err - é”™è¯¯å¯¹è±¡
     * @param {File} file - ä¸Šä¼ çš„æ–‡ä»¶å¯¹è±¡
     * @param {Array} fileList - æ–‡ä»¶åˆ—è¡¨
     */
    handleUploadError(err, file, fileList) {
      console.log(file, fileList)
      console.error('ä¸Šä¼ å¤±è´¥:', err);
      this.$message.error('å›¾ç‰‡ä¸Šä¼ å¤±è´¥ï¼Œè¯·é‡è¯•ï¼');
    },
    
    /**
     * ä¸Šä¼ å‰çš„é’©å­ï¼Œè¿›è¡Œæ–‡ä»¶ç±»å‹å’Œå¤§å°çš„éªŒè¯
     * @param {File} file - ä¸Šä¼ çš„æ–‡ä»¶å¯¹è±¡
     * @returns {Boolean} - æ˜¯å¦å…è®¸ä¸Šä¼ 
     */
    beforeAvatarUpload(file) {
      const isImage = ['image/jpeg', 'image/png', 'image/gif'].includes(file.type);
      if (!isImage) {
        this.$message.error('ä¸Šä¼ å›¾ç‰‡åªèƒ½æ˜¯ JPG/PNG/GIF æ ¼å¼!');
      }
      const isLt5M = file.size / 1024 / 1024 < 5;
      if (!isLt5M) {
        this.$message.error('ä¸Šä¼ å›¾ç‰‡å¤§å°ä¸èƒ½è¶…è¿‡ 5MB!');
      }
      return isImage && isLt5M; // è¿”å› true å…è®¸ä¸Šä¼ ï¼Œå¦åˆ™é˜»æ­¢ä¸Šä¼ 
    },


    // å¼€å§‹æ‹–æ‹½
    startDragging(event) {
      console.log("å¼€å§‹æ‹–æ‹½", event);
      this.isDragging = true;
      this.startX = event.clientX; // è®°å½•æ‹–æ‹½å¼€å§‹çš„ X åæ ‡
      this.startY = event.clientY; // è®°å½•æ‹–æ‹½å¼€å§‹çš„ Y åæ ‡
      this.startProgress = this.progress; // è®°å½•æ‹–æ‹½å¼€å§‹æ—¶çš„è¿›åº¦
      this.updateProgress(event); // åˆå§‹æ—¶æ›´æ–°è¿›åº¦
    },
    // æ‹–æ‹½æ—¶è°ƒæ•´è¿›åº¦
    dragProgress(event) {
      if (this.isDragging) {
        this.updateProgress(event); // æ›´æ–°è¿›åº¦
      }
    },
    // åœæ­¢æ‹–æ‹½
    stopDragging(event) {
      if (this.isDragging) {
        this.isDragging = false; // å–æ¶ˆæ‹–æ‹½çŠ¶æ€

        const deltaX = Math.abs(event.clientX - this.startX);
        const deltaY = Math.abs(event.clientY - this.startY);
        const totalDisplacement = Math.sqrt(deltaX ** 2 + deltaY ** 2); // è®¡ç®—ä½ç§»è·ç¦»

        if (totalDisplacement > 10) {
          console.log("æ‹–æ‹½å®Œæˆï¼Œå½“å‰è¿›åº¦ï¼š", this.progress);
          this.triggerFunction(this.progress); // è§¦å‘ç›¸å…³å‡½æ•°
        } else {
          console.log("æ‹–æ‹½å–æ¶ˆ");
          this.progress = this.startProgress; // æ¢å¤è¿›åº¦
        }
       
      }
    },
    updateProgress(event) {
      // å¦‚æœå½“å‰ä½ç½®å’Œå¼€å§‹ä½ç½®ç›¸åŒï¼Œä¸æ›´æ–°è¿›åº¦
      if (event.clientX === this.startX && event.clientY === this.startY) {
        return;
      }

      {
        const deltaX = Math.abs(event.clientX - this.startX);
        const deltaY = Math.abs(event.clientY - this.startY);
        const totalDisplacement = Math.sqrt(deltaX ** 2 + deltaY ** 2); // è®¡ç®—ä½ç§»è·ç¦»
        // å¦‚æœä½ç§»è·ç¦»å°äº 10ï¼Œä¸æ›´æ–°è¿›åº¦
        if (totalDisplacement < 10) {
          return;
        }
      }
      
      const progressBar = event.target.getBoundingClientRect(); // è·å–è¿›åº¦æ¡è¾¹ç•Œ
      const centerX = progressBar.left + progressBar.width / 2; // åœ†å¿ƒ X åæ ‡
      const centerY = progressBar.top + progressBar.height / 2; // åœ†å¿ƒ Y åæ ‡

      // é¼ æ ‡ç›¸å¯¹äºåœ†å¿ƒçš„åç§»
      const deltaX = event.clientX - centerX;
      const deltaY = event.clientY - centerY;

      // è®¡ç®—è§’åº¦ï¼Œatan2 è¿”å›å€¼èŒƒå›´ä¸º [-PI, PI]ï¼Œæˆ‘ä»¬éœ€è¦è½¬æ¢ä¸º [0, 360]
      let angle = Math.atan2(deltaY, deltaX) * (180 / Math.PI); // è½¬æ¢ä¸ºè§’åº¦

      // è°ƒæ•´è§’åº¦èŒƒå›´ï¼Œä½¿æ­£ä¸Šæ–¹ä¸º 0 åº¦
      angle = (angle + 360 + 90) % 360; // åŠ  90 åº¦ï¼Œä½¿æ­£ä¸Šæ–¹ä¸º 0 åº¦

      // æ ¹æ®è°ƒæ•´åçš„è§’åº¦è®¡ç®—è¿›åº¦ç™¾åˆ†æ¯”
      this.progress = Math.round((angle / 360) * 100);
      
    },
    // è§¦å‘å¯¹åº”å‡½æ•°
    triggerFunction(value) {
      console.log("å½“å‰è¿›åº¦ï¼š", value);
      // æ ¹æ®progressè®¡ç®—åœ¨questionsä¸­çš„index
      const index = Math.round((value / 100) * this.questions.length);
      // è®¾ç½®é¢˜å· 
      this.currentQuestionIndex = index;
      // æ›´æ–°currentQuestionIndexInNav
      if (this.questions[index].hidden) {
        this.currentQuestionIndexInNav = -1; // ä¸å¯¹åº”é¢˜å·å¯¼èˆª
      } else {
        this.currentQuestionIndexInNav = Number(this.questions[index].index) - 1;
      }

    },
  },
};
</script>


